import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

// const originalPush = VueRouter.prototype.push
// VueRouter.prototype.push = function push(location, onResolve, onReject) {
// if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
// return originalPush.call(this, location).catch(err => err)
// }
const routes = [

  {
    path:"/login",
    name: "login",
    component: () => import("../views/login.vue")
  },
  {
    path: '/',
    // name: 'Home',
    component: Home,
    children: [
      {
        path: "/",
        redirect: "/productManage"
      },
      {
        path: "/productManage",
        name: "productManage",
        component: () => import("../views/productManage/index.vue")
      },
      {
        path: "/activeCodeManage",
        name: "activeCodeManage",
        component: () => import("../views/activeCodeManage/index.vue")
      },
      {
        path: "/cardManage",
        name: "cardManage",
        component: () => import("../views/cardManage/index.vue")
      },
      {
        path: "/addProduct",
        name: "addProduct",
        component: () => import("../views/addProduct/index.vue")
      },
      {
        path: "/productDetail/:id",
        name: "productDetail",
        component: () => import("../views/productDetail/index.vue")
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
router.beforeEach((to, from, next) => {
  if(to.name == 'login' || to.query.out == "true") {
    next();
    return;
  }
  // debugger;
  if(sessionStorage.getItem("isLogin") == "true") {
    next();
  }else {
    next("login")
  }

})
export default router
