import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    routeName: {
      name: "产品管理",
      path: "/productManage"
    },
    userName: ""
  },
  mutations: {
    setRouteName(state, value) {
      state.routeName = value;
      localStorage.setItem("store",JSON.stringify(state))
    },
    setUserName(state, value) {
      state.userName = value;
      localStorage.setItem("store",JSON.stringify(state))
    }
  },
  actions: {
  },
  modules: {
  }
})
